.wechat {
    position: fixed;
    right: 80px;
    top: 130px;
  }
  .wechat img {
    @apply w-28;
  }
  .wechat-subtitle {
    font-size: 0.8rem;
    @apply mt-2 text-sm;
  }
  @media (max-width: 1024px) {
    .wechat {
      right: 10px;
      top: 10px;
      position: relative;
      @apply flex flex-row items-center m-4;
    }
    .wechat img {
      @apply w-36;
    }
    .wechat-subtitle {
      @apply ml-2 text-base;
    }
  }