.container {
  --font-color: #ccc;
  @apply flex flex-row items-center justify-center mb-4;
}
.container > div {
  @apply flex flex-row items-center;
}
.step {
  @apply flex flex-row items-center;
  @apply cursor-pointer;
}
.step-active {
  --font-color: var(--main-color);
}
.step-circle {
  @apply flex items-center justify-center;
  @apply w-6 h-6 text-lg;
  @apply rounded-full border-2;
  color: var(--font-color);
  border-color: var(--font-color);
}
.step-circle-active {
  @apply border-none;
  background-color: var(--main-color);
  color: white;
}
.label {
  @apply ml-2 text-lg;
  color: var(--font-color);
}
.line {
  @apply flex-1 mx-8;
  @apply h-0.5 w-16 rounded-sm;
  background-color: var(--font-color);
}
.lineActive {
  background-color: var(--main-color);
}
@media (max-width: 768px) {
  .container {
    @apply w-full;
  }
  .step-circle {
    @apply w-6 h-6 text-base;
  }
  .label {
    @apply ml-1 text-base whitespace-nowrap;
  }
  .line {
    @apply mx-2;
  }
}
